import React from "react";
import { Select } from "antd";
import { useQuery } from 'react-query3';

const { Option } = Select;

export default function TimezoneSelect(props) {

    const { data: timezones, isLoading } = useQuery<Array<string>>(`/schedule/timezone`, {
        refetchOnWindowFocus: false,
    });

    return (
        <Select {...props} loading={isLoading}
            showSearch
            filterOption={(input, option) =>
                option.value && option.value.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }>
            <Option key="none" value={null}>
                System Default
            </Option>
            {timezones?.map((option) => (
                <Option key={option} value={option}>
                    {option}
                </Option>
            ))}
        </Select>
    );
}